import React from 'react'
import { Bio } from './style'

const Biography = () => {
    return (
        <Bio className="section">
            <div className="container">
                <h2 className="title">Biografia</h2>
                <div className="textbox">
                    <p className="text">
                    Cria da BXD, DJ Sandinho é conhecido por ser o Alquimista das Aceleradas. 
                    Unindo o mais avançado ritmo do atabaque carioca com breakbeats clássicos de rock e soul,
                    suas músicas e apresentações deixam evidente seu amor pela música e pelos diferentes tipos de tambores.
                    <br></br>
                    Suas apresentações de funk são sempre marcantes, começando com funks mais pesados e acelerados do Rio de Janeiro, 
                    mas também transita entre ritmos mais leves e melódicos (como os funks de 2019) ou mais atuais, como funks do tiktok, 
                    pra formar uma apresentação com repertório amplo e transições limpas, sem pausas.
                    Assim, Sandinho se estabeleceu como figura presente nos eventos universitários da Rural, 
                    já tendo participado em eventos com DJ Julia Bacellar, Bonde das Maravilhas e DJ Sexy Love.
                    <br></br>
                    O DJ também é ativo na cena underground de música eletrônica brasileira, 
                    através do selo independente ACABOU O SOSSEGO, estabelecido por ele e outros artistas. 
                    O selo conta hoje com 3 lançamentos entre o jungle/drum n bass e o funk, e conta com 
                    artistas de vários estados do Brasil, além de uma radio no YouTube focada em drum n bass.
                    <br></br>
                    Seu lançamento mais recente, “AQUECIMENTO DA ALQUIMIA” conta com mais de 300 streamings no Spotify,
                    e seu segundo single, “REI DO WHISKY” com MC Negritin,  está planejado para Agosto de 2023.
                    <br></br>
                    Escute AGORA a prévia da nova música:
                    </p>
                </div>
            </div>
        </Bio>
    )
}

export default Biography
